.cover {
  @include cover-text;
  background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/catalog/background-catalog.png");
  background-repeat: no-repeat;
  background-size: 100%;
  width: 280px;
  height: 290px;
  margin: 0;
  padding-top: 65px;

  @media (min-resolution: 2dppx) {
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/catalog/background-catalog@2x.png");
  }

  @media (min-width: $tablet-width) and (max-width: 1200px) {
    margin-top: 69px;
    width: 100%;
    height: 200px;
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/catalog/background-catalog-tablet.png");
    background-size: 100%;
    display: flex;

    @media (min-resolution: 2dppx) {
      background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/catalog/background-catalog-tablet@2x.png");
    }
  }
}

.cover-image {
  display: block;
  margin: 0 110px 0;
}

.cover__message {
  display: block;
  text-align: center;
  margin: 0;
  padding-top: 40px;

  @media (min-width: $tablet-width)  and (max-width: 1200px) {
    padding: 10px 124px 0;
    margin-left: auto;
    text-align: left;
    background-position: 10% 50%;
  }
}
